import { ADD, INCREASE, DECREASE, REMOVE } from './type';

const initialState = {
	name: 'max',
	itemsInCart: 0,
	totalPrice: 0,
	items: []
};

export const reducer = (state = initialState, action) => {
	switch (action.type) {
		case ADD:
			return {
				...state,
				items: [ ...state.items, action.payload ],
				itemsInCart: state.itemsInCart + 1,
				totalPrice: state.totalPrice + action.payload.price
			};
		case INCREASE:
			const findItIncrease = state.items.find((any) => any.title === action.payload);

			return {
				...state,
				itemsInCart: state.itemsInCart + 1,
				totalPrice: state.totalPrice + findItIncrease.price,
				items: state.items.map((each) => {
					const newItem = { ...each };
					if (newItem.title === action.payload) {
						newItem.inCart += 1;
					}
					return newItem;
				})
			};
		case DECREASE:
			const findItDecrease = state.items.find((any) => any.title === action.payload);

			return {
				...state,
				itemsInCart: state.itemsInCart - 1,
				totalPrice: state.totalPrice - findItDecrease.price,

				items: state.items.map((each) => {
					const newItem = { ...each };
					if (newItem.title === action.payload) {
						newItem.inCart -= 1;
					}
					return newItem;
				})
			};
		case REMOVE:
			const filter = state.items.filter((any) => any.title !== action.payload);
			const findIt = state.items.find((any) => any.title === action.payload);
			return {
				...state,
				totalPrice: state.totalPrice - findIt.price * findIt.inCart,
				itemsInCart: state.itemsInCart - findIt.inCart,
				items: filter
			};
		default:
			return state;
	}
};
